import { Component, OnInit, OnDestroy } from '@angular/core';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { ActivatedRoute } from '@angular/router';
import { Product } from './../../../core/models/product';
import { ProductService } from './../../../core/services/product.service';
import { DecimalPipe } from '@angular/common';
import { UserService } from '../../../user/services/user.service'

@Component({
  selector: 'app-product-detail-page',
  templateUrl: './product-detail-page.component.html',
  styleUrls: ['./product-detail-page.component.css']
})
export class ProductDetailPageComponent implements OnInit, OnDestroy {
  actionsSubscription: Subscription;
  product$: Product = null;
  routeSubs: Subscription;
  productSubs: Subscription;
  productId: any;
  currentUser$: any;

  constructor(private productService: ProductService,
              private route: ActivatedRoute,
              private userService: UserService) {

  /**On Init
   * 1. Parse route params
   * 2. Retrive product id
   * 3. Ask for the product detail based on product id 
   * */
    this.actionsSubscription = this.route.params.subscribe(
      (params: any) => {
        this.productId = params['id'];
        this.productSubs = this.productService
          .getProduct(this.productId)
          .subscribe(response => this.product$ = response);
      }
    );
    if (localStorage.getItem('user')){
      this.userService.getUser().subscribe(response => {
        this.currentUser$ = response;
      });
    } else{
      this.currentUser$ = false;
    }
  };


  ngOnInit() {
  }

  ngOnDestroy() { 
    if (this.actionsSubscription ) this.actionsSubscription.unsubscribe();
    if (this.productSubs) this.productSubs.unsubscribe();
  }
  /**
   * Action To be dispatched
   * when added to cart
   */
  // addToCart(){
  //   return;
  // }

}



