import { NgModule } from '@angular/core';
import { SharedModule } from './../shared/index';

import { RouterModule } from '@angular/router';

// For Temp Puropose
// TODO: Remove this from here
import { ProductService } from './../core/services/product.service';

// Components
import { ProductDetailPageComponent } from './components/product-detail-page/product-detail-page.component';
import { ProductDetailsComponent } from './components/product-detail-page/product-details/product-details.component';
import { ProductDescriptionComponent } from './components/product-detail-page/product-description/product-description.component';
import { ProductImagesComponent } from './components/product-detail-page/product-images/product-images.component';
import { ProductPriceInfoComponent } from './components/product-detail-page/product-price-info/product-price-info.component';
import { ProductVariantsComponent } from './components/product-detail-page/product-variants/product-variants.component';
import { ProductComponent } from './product.component';
import { FileDropModule } from 'ngx-file-drop/lib/ngx-drop';
import { ProductFileUploadComponent } from './components/product-detail-page/product-file-upload/product-file-upload.component'
import { FileUploadModule } from 'ng2-file-upload';
import { ProductOptionSelectComponent } from './components/product-detail-page/product-option-select/product-option-select.component';
import { ProductOptionComponent } from './components/product-detail-page/product-option/product-option.component';
import { ProductGroupComponent } from './components/product-detail-page/product-group/product-group.component';
import { ProductTextComponent } from './components/product-detail-page/product-text/product-text.component';
import { ProductTypeDescriptionComponent } from './components/product-detail-page/product-type-description/product-type-description.component';
import { ProductCheckboxComponent } from './components/product-detail-page/product-checkbox/product-checkbox.component';
// import { ToHtmlPipe } from './../shared/pipes/to-html.pipe';
// Routes
import { ProductRoutes as routes } from './product.routes';

// Effects
import { EffectsModule } from '@ngrx/effects';
import { ProductEffects } from './effects/product.effects';


@NgModule({
  declarations: [
    // components
    ProductDetailPageComponent,
    ProductComponent,
    ProductDetailsComponent,
    ProductImagesComponent,
    ProductPriceInfoComponent,
    ProductDescriptionComponent,
    ProductVariantsComponent,
    ProductFileUploadComponent,
    ProductOptionSelectComponent,
    ProductOptionComponent,
    ProductGroupComponent,
    ProductTextComponent,
    ProductTypeDescriptionComponent,
    ProductCheckboxComponent,
    // ToHtmlPipe
    // pipes
  ],
  exports: [
    // components
    ProductDetailPageComponent,
    ProductDetailsComponent,
    ProductImagesComponent,
    ProductPriceInfoComponent,
    ProductDescriptionComponent,
    ProductVariantsComponent
  ],
  imports: [
    SharedModule,
    RouterModule.forChild(routes),
    FileDropModule,
    FileUploadModule
  ],
  providers: [
  ]
})
export class ProductModule {}



