import { Component, OnInit, Input, Output, EventEmitter} from '@angular/core';
import { VariantParserService } from './../../../../core/services/variant-parser.service';
import { environment } from './../../../../../environments/environment';

@Component({
  selector: 'app-product-option',
  templateUrl: './product-option.component.html',
  styleUrls: ['./product-option.component.scss']
})
export class ProductOptionComponent implements OnInit {
  @Input() variantOptionHash: any;
  @Input() currentSelectedOptions = {};
  @Output() onOptionClickEvent = new EventEmitter();
  @Input() optionTypes: any;

  constructor(
    private variantParserService: VariantParserService
  ) { }

  ngOnInit() {
  }

  onOptionClick(event) {
    this.onOptionClickEvent.emit(event);
  }

  getProductImageUrl(url) {
    var temp = url + "";
    if (temp.charAt(temp.length - 1) == '/')
      return false; //insert empty image
    else
      return environment.API_ENDPOINT + url;
  }
}



