import { Component, OnInit, Input } from '@angular/core';
import { environment } from './../../../../../environments/environment';

@Component({
  selector: 'app-product-option-select',
  templateUrl: './product-option-select.component.html',
  styleUrls: ['./product-option-select.component.scss']
})
export class ProductOptionSelectComponent implements OnInit {
  @Input() currentSelectedOptions: any;
  @Input() type;
  focus_type: any;

  constructor() { }

  ngOnInit() {
    if (this.type.children.length != 0)
      this.focus_type = this.type.children[0];
  }

  isDisabled(){
    for (let key in this.currentSelectedOptions){
      if (this.type.show_option_value == this.currentSelectedOptions[key].id)
        return false;
      else if (this.type.hide_option_value == this.currentSelectedOptions[key].id)
        return true;
    }
    if (this.type.show_option_value)
      return true;
    else if (this.type.hide_option_value)
      return false;
  }
}



