import { AppState } from './../../../../interfaces';
import { Store } from '@ngrx/store';
import { CheckoutActions } from './../../../../checkout/actions/checkout.actions';
import { getAdjustmentOrder } from './../../../../checkout/reducers/selectors';
import { Variant } from './../../../../core/models/variant';
import { VariantRetriverService } from './../../../../core/services/variant-retriver.service';
import { Component, OnInit, Input } from '@angular/core';
import { Product } from './../../../../core/models/product';
import { VariantParserService } from './../../../../core/services/variant-parser.service';
import { DecimalPipe } from '@angular/common';
import { Router } from '@angular/router';
import { CheckoutService } from './../../../../core/services/checkout.service';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.scss']
})
export class ProductDetailsComponent implements OnInit {
  @Input() product: Product;
  @Input() user: any;
  variantOptionHash: any;
  currentSelectedOptions = {};
  description: any;
  images: any;
  productPrice: number;
  original_line_item: string;

  constructor(private variantParser: VariantParserService,
    private variantRetriver: VariantRetriverService,
    private checkoutActions: CheckoutActions,
    private checkoutService: CheckoutService,
    private store: Store<AppState>,
    private router: Router) {
  }

  ngOnInit() {
    this.description = this.product.description;
    this.images = this.product.master.images;
    this.getOrderedPrice(this.getProductPrice());

    this.variantOptionHash = this.variantParser
                                      .getVariantOptionHash(this.product.option_types, this.product.product_variant_types);
    this.store.select(getAdjustmentOrder).subscribe(res => {
      this.original_line_item = res;
    });
  }

  /**
   * 
   *                   
   *                   
   */
  onOptionClick(option) {
    
    const selectedOptions = new VariantRetriverService()
        .getCurrentSelectedOptions(option, this.currentSelectedOptions);

    this.getOrderedPrice(this.getProductPrice());
   }

  addToCart(product: Product) {
    console.log("send cart");
    let line_item = {
      variant_id: this.product.master.id,
      price: this.productPrice,
      origin: this.original_line_item
    }
    this.checkoutService.createNewLineItem(line_item)
    .subscribe( line_item => {
      return this.store.dispatch(this.checkoutActions.addToCartSuccess(line_item));
    })
    .add(() => {
      this.router.navigate(['/checkout', 'cart']);
    })
  }

  getProductPrice(){
    for (var i = 0; i < this.product.product_prices.length; i ++){
      if (this.product.product_prices[i].product_price_set_id == this.user.product_price_set_id){
        return this.product.product_prices[i].price;
      }
    }
    return parseFloat(this.product.price);
  }

  getOrderedPrice(price: any){
    for (let key in this.currentSelectedOptions){
      let value = this.variantOptionHash[key][this.currentSelectedOptions[key].name];
      if (value) 
        price += value.price;
    }
    this.productPrice = price;
  }
}



